___CSS_LOADER_EXPORT___.push([module.id, ".login-form[data-v-49fb850d]{max-width:600px}.login_bg[data-v-49fb850d]{background:radial-gradient(circle at 50% 50%,#ed1c24,#ffd503)}.btn_color[data-v-49fb850d]{background:#e63546!important;color:#fff}.label_color[data-v-49fb850d]{color:#e63546}", "",{"version":3,"sources":["webpack://./resources/js/auth/pages/reset_password.vue"],"names":[],"mappings":"AAoJA,6BACA,eACA,CAEA,2BAEA,6DACA,CAEA,4BAEA,4BAAA,CADA,UAEA,CAEA,8BACA,aACA","sourcesContent":["<template>\r\n  <v-app class=\"login_bg\">\r\n    <v-main>\r\n      <v-container fill-height>\r\n        <v-layout align-center justify-center>\r\n          <v-flex class=\"login-form text-xs-center\">\r\n            <v-card>\r\n              <!-- Error -->\r\n              <v-alert v-if=\"error\" shaped prominent type=\"error\" dismissible>{{\r\n                errorMsg\r\n              }}</v-alert>\r\n              <v-card-text class=\"pt-2\">\r\n                <div class=\"d-flex align-items-center justify-content-center\">\r\n                  <img\r\n                    src=\"/all-assets/common/logo/cpb/cpfivestar.png\"\r\n                    alt\r\n                    height=\"50px\"\r\n                  />\r\n                  <h3 class=\"text-center label_color ml-4\">Reset Password</h3>\r\n                </div>\r\n\r\n                <form @submit.prevent=\"resetPass()\">\r\n                  <v-text-field\r\n                    v-model=\"form.login\"\r\n                    light\r\n                    prepend-icon=\"mdi-account-key\"\r\n                    label=\"Password Reset ID\"\r\n                    type=\"number\"\r\n                    placeholder=\"Enter Your Operator / Owner Login ID\"\r\n                    :rules=\"[(v) => !!v || 'Login ID is required!']\"\r\n                    required\r\n                  ></v-text-field>\r\n\r\n                  <v-btn\r\n                    v-if=\"form.personal_contact == ''\"\r\n                    small\r\n                    block\r\n                    color=\"primary\"\r\n                    :loading=\"loading\"\r\n                    type=\"submit\"\r\n                  >\r\n                    Check\r\n                    <v-icon right>mdi-magnify</v-icon>\r\n                  </v-btn>\r\n\r\n                  <v-btn\r\n                    v-if=\"form.personal_contact\"\r\n                    small\r\n                    :loading=\"loading\"\r\n                    block\r\n                    type=\"submit\"\r\n                    class=\"btn_color\"\r\n                  >\r\n                    Send Password to Number\r\n                    <v-icon right>mdi-phone-message-outline</v-icon>\r\n                  </v-btn>\r\n                </form>\r\n\r\n                <div\r\n                  class=\"d-flex align-items-center justify-content-between mt-2\"\r\n                >\r\n                  <router-link :to=\"{ name: 'Login' }\" block class=\"btn\">\r\n                    Login <v-icon rigth>mdi-login-variant</v-icon>\r\n                  </router-link>\r\n\r\n                  <div class=\"small\">\r\n                    <img\r\n                      src=\"/all-assets/common/logo/cpb/cpbit.png\"\r\n                      alt=\"\"\r\n                      height=\"30px\"\r\n                    />\r\n                    Powered by CPB-IT\r\n                  </div>\r\n                </div>\r\n              </v-card-text>\r\n            </v-card>\r\n          </v-flex>\r\n        </v-layout>\r\n      </v-container>\r\n    </v-main>\r\n  </v-app>\r\n</template>\r\n\r\n<script>\r\n// vform\r\nimport Form from \"vform\";\r\n\r\nexport default {\r\n  data() {\r\n    return {\r\n      passwordType: false,\r\n      loading: false,\r\n      password: null,\r\n\r\n      error: false,\r\n      errorMsg: \"\",\r\n\r\n      // Form\r\n      form: new Form({\r\n        login: \"\",\r\n        personal_contact: \"\",\r\n      }),\r\n    };\r\n  },\r\n\r\n  methods: {\r\n    // Reset Password\r\n    resetPass() {\r\n      this.loading = true;\r\n      this.form\r\n        .post(\"/reset_pass\")\r\n        .then((response) => {\r\n          console.log(response.data);\r\n          let resData = response.data;\r\n\r\n          // this.form.personal_contact = response.data;\r\n          // this.readonly = true;\r\n\r\n          this.loading = false;\r\n\r\n          // Error\r\n          if (resData.status == \"error\") {\r\n            console.log(resData);\r\n            this.error = true;\r\n            this.errorMsg = resData.msg;\r\n          } else {\r\n            this.$router.push({\r\n              name: \"otp_input\",\r\n              params: {\r\n                number: response.data.data,\r\n                reset_type: response.data.reset_type,\r\n                cv_code: response.data.cv_code,\r\n              },\r\n            });\r\n          }\r\n        })\r\n        .catch((error) => {\r\n          this.loading = false;\r\n\r\n          console.log(error);\r\n        });\r\n    },\r\n  },\r\n};\r\n</script>\r\n\r\n\r\n<style scoped>\r\n.login-form {\r\n  max-width: 600px;\r\n}\r\n\r\n.login_bg {\r\n  /* background: #990f02; */\r\n  background: radial-gradient(circle at 50% 50%, #ed1c24, #ffd503);\r\n}\r\n\r\n.btn_color {\r\n  color: white;\r\n  background: #e63546 !important;\r\n}\r\n\r\n.label_color {\r\n  color: #e63546;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
